import {
    mapState,
    mapMutations
} from "vuex";
import {
    dslArray,
    rules
} from "./mock.js";

export default {
    mounted() {
        console.log("Main.js default ")
    },
    data() {
        return {
            rules,
            dslArray,
            activeNames: []
        };
    },
    computed: {
        ...mapState(["currentModel", "currentModelAdd"])
    },
    methods: {
        ...mapMutations(["setCurrentModel", "setCurrentModelAdd"]),
        handleChange(activeNames) {
            console.log("activeNames", activeNames, activeNames === this.activeNames);
        },
        onSubmit() {
            console.log("submit!", this.form);
        },
        showCurrentN() {
            let vm = this;
            this.dslArray.map((panel, index) => {
                let formVM = vm.$refs["nhjs" + index][0];

                formVM.validate((valid, obj) => {
                    console.log(obj);
                    if (valid) {
                        console.log("submit!", this.dslArray);
                    } else {
                        console.log("error submit!!");
                        return false;
                    }
                });
            });
        }
    },
    watch: {

    },
    components: {
        CTable: () => import( /* webpackChunkName: "FormTable" */ "@c/FormTable.vue")
    }

}