<template>
  <div class="view column">
    <div class="tool-bar fixed">
      <el-button @click="goTo('home')">放弃编辑</el-button>
      <el-button @click="save" type="primary" class="button">保存</el-button>
    </div>
    <div class="tool-bar"></div>

    <el-collapse v-model="activeNames" @change="handleChange">
      <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
      <el-collapse-item
        :title="panelDsl.title"
        :name="panelDsl.title"
        v-for="(panelDsl, panelDslIndex) in dslArray"
        :key="'panelDsl'+panelDslIndex"
      >
        <c-table
          class="box-card"
          v-model="currentModelAdd"
          :dsl="panelDsl.rows"
          :rules="rules"
          :tableInfo="panelDsl"
          ref="table"
        ></c-table>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import main from "./Main.js";
export default {
  name: "Add",
  mixins: [main],
  mounted() {
    console.log("nhjs");
    this.activeNames = this.dslArray.map(panel => {
      return panel.title;
    });
  },
  data() {
    return {};
  },
  methods: {
    goTo(command) {
      if (command === "home") {
        this.$confirm("是否放弃编辑?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$router.push({ name: command });
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    save() {
      function isOk(tables) {
        let count = tables.length;
        let isAllOk = true;
        return new Promise(resolve => {
          tables.map(table => {
            table.valid().then(isSuccess => {
              --count;
              isAllOk = isAllOk && isSuccess;
              if (!isAllOk || count === 0) resolve(isAllOk);
            });
          });
        });
      }
      /* 验证表单是否都符合rule */
      isOk(this.$refs.table).then(isAllOk => {
        let doc = this.currentModelAdd;
        if (isAllOk) {
          const loading = this.$loading();
          console.log("this.currentModelAdd");
          if (this.$route.name === "update") {
            this.setCurrentModel(doc);
            this.$post("/scha-web/a/fnbj/hqbjnhj/update", doc).then(
              response => {
                if (response.isSuccess) {
                  /* 如果是修改，需要更新currentModel */

                  loading.close();
                  this.$router.push({ name: "home" });
                  this.$message({
                    showClose: true,
                    message: "保存成功",
                    type: "success"
                  });
                } else {
                  this.$message({
                    showClose: true,
                    message: "保存失败",
                    type: "warning"
                  });
                }
              }
            );
          }
          /* save */
          this.$post("/proxy/save", {
            coll: "hqbj_hj_nan" /* collection */,
            doc /* document */
          }).then(response => {
            if (response.isSuccess) {
              /* 如果是修改，需要更新currentModel */

              loading.close();
              this.$router.push({ name: "home" });
              this.$message({
                showClose: true,
                message: "保存成功",
                type: "success"
              });
            } else {
              this.$message({
                showClose: true,
                message: "保存失败",
                type: "warning"
              });
            }
          });
        } else {
          this.$message({
            showClose: true,
            message: "请按照提示检查输入项",
            type: "warning"
          });
        }
      });
    }
  },
  watch: {}
};
</script>

