<template>
  <div class="view column">
    <div class="tool-bar fixed">
      <el-button @click="goTo('add')" :disabled="true">新增</el-button>
      <el-button @click="goTo('update')">修改</el-button>
      <el-button @click="goTo('remove')" :disabled="true">删除</el-button>
    </div>
    <div class="tool-bar"></div>

    <el-collapse v-model="activeNames" @change="handleChange">
      <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
      <el-collapse-item :title="'查询'" :name="'query'">
        <c-query-panel
          v-model="modelQuery"
          :dsl="dslArrayQuery[0].rows"
          :tableInfo="{tableHeader:tableHeaderQuery,tableModel:tableModelQuery}"
          @queryClick="query"
          @row-dblclick="handleDblclick"
        ></c-query-panel>
      </el-collapse-item>
      <!-- {{currentModel}} -->
      <el-collapse-item
        :title="panelDsl.title"
        :name="panelDsl.title"
        v-for="(panelDsl, panelDslIndex) in dslArray"
        :key="'panelDsl'+panelDslIndex"
      >
        <!--  -->
        <c-table
          class="box-card"
          v-model="currentModel"
          :dsl="panelDsl.rows"
          :rules="rules"
          :tableInfo="panelDsl"
          :disabled="true"
        ></c-table>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import mainjs from "./Main.js";
import {
  modelQuery,
  dslArrayQuery,
  tableHeaderQuery,
  tableModelQuery
} from "./mock.js";
import $ from "jquery";
export default {
  name: "HqbjHjNan",
  mixins: [mainjs],
  mounted() {
    console.log("Main.js ");
    this.activeNames = ["query"];
    this.$get("/scha-web/a/fnbj/hqbjnhj/list")
      .then(res => {
        console.log(res);
        let dataRemote = res.list[0];
        let except = [];
        Object.keys(this.currentModel).map(key => {
          if (!Object.prototype.hasOwnProperty.call(dataRemote, key)) {
            except.push(key);
          }
        });
        console.log("except", except);
        debugger;
        this.tableModelQuery = res.list;
      })
      .catch(error => {
        console.log(error);
      });
  },
  data() {
    return {
      modelQuery,
      dslArrayQuery,
      tableHeaderQuery,
      tableModelQuery
    };
  },
  methods: {
    goTo(command) {
      if (command === "add") {
        /* 初始化新增model */
        this.setCurrentModelAdd(
          (model => {
            let obj = {};
            for (let key in model) {
              obj[key] = "";
            }
            return obj;
          })(this.currentModelAdd)
        );
        return this.$router.push({
          name: command
        });
      }
      if (command === "update") {
        /* 初始化新增model */
        this.setCurrentModelAdd(
          (model => {
            return { ...model };
          })(this.currentModel)
        );
        return this.$router.push({
          name: command
        });
      }
      if (command === "remove") {
        this.$confirm(
          `确实要删除编号为${this.currentModel.bh}的表单?`,
          "提示",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }
        )
          .then(() => {
            const loading = this.$loading();
            /* 发送this.currentModel到后台处理删除请求 */
            console.log(this.currentModel);
            setTimeout(() => {
              this.afterRemove(loading);
            }, 1000 * 2);
          })
          .catch(error => {
            console.log("cancel", error);
          });
      }
    },
    /* query panel */
    query() {
      debugger;
      const loading = this.$loading();

      console.log("queryClick", this.modelQuery);
      let doc = this.modelQuery;
      this.$post("/scha-web/a/fnbj/hqbjnhj/query", doc).then(res => {
        debugger;
        if (res.isSuccess) {
          // let dataRemote = res.list;
          /* let except = [];
          Object.keys(dataRemote).map(key => {
            if (
              !Object.prototype.hasOwnProperty.call(this.tableModelQuery, key)
            ) {
              except.push(key);
            }
          });
          console.log("except", except); */
          this.tableModelQuery = res.list;
        }
        loading.close();
      });
    },
    handleDblclick(row, event) {
      console.log(row, event);
      this.setCurrentModel(row);
      /* 打开详情折叠板 */
      this.activeNames = this.dslArray.map(panel => {
        return panel.title;
      });
    },
    /* query panel */
    /* remove */
    afterRemove(loading) {
      /* 置空 */
      this.setCurrentModel({});
      /* 展开面板为查询 */
      this.activeNames = ["query"];
      loading.close();
      this.$message({
        showClose: true,
        message: "删除成功",
        type: "success"
      });
      /* 刷新列表 */
      this.query();
    }
    /* remove */
  },
  watch: {
    currentModel(newModel) {
      console.log("newModel", newModel);
    }
  },
  components: {
    CQueryPanel: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/QueryPanel.vue")
    // CPanelItem: () => import(/* webpackChunkName: "PanelItem" */ "@c/views/PanelItem.vue"),
  }
};
</script>


